* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.container {
  max-width: 80%;
  margin: 0 auto;
}
.wrapper {
  width: 100vw;
  height: 100vh;
  background: #edf7ff;
}
.select {
  margin-top: 4vw;
  height: 3.1vw;
  width: 40%;
  font-size: 1.8vw;
}

.list {
  overflow: auto;
  height: calc(100vh - 11.8vw);
}

.list__wrapper {
  display: flex;
  background: white;
  margin-top: 1.8vw;
  padding: 1.2vw 1.8vw;
  border-radius: 0.8vw;
  justify-content: space-between;
  align-items: center;
}

.list__day {
  font-size: 1.8vw;
}
.list__wrapper:first-child {
  margin-top: 0;
}

.child__wrapper {
  display: flex;
  flex-direction: column;
  width: 48%;
}
.childs {
  display: flex;
  font-size: 1vw;
  justify-content: space-between;
}
.childs:first-child {
  margin-bottom: 0.5vw;
}

.child__value {
  padding: 0.25vw;
  background: #edf7ff;
}

.list__right {
  display: flex;
  width: 50%;
  justify-content: space-between;
}

.main {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-top: 4vw;
}
.main__left {
  width: 68%;
}
.main__right {
  width: 30%;
  background-color: #1bbc9b;
  height: calc(100vh - 23vw);
  text-align: center;
}
.main-right__title {
  color: #edf7ff;
  margin-top: 1.9vw;
  font-size: 1.5vw;
}
.main-right__count {
  font-size: 4vw;
  color: white;
  margin: 5.4vw 0;
}
